@import '../../assets/styles/colors.scss';

.pageContainer {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-rows: auto 1fr auto;

  .main {
    background: #f5f6f8;
    z-index: -1;
    padding: 8px;

    .title {
      font-size: 29px;
      letter-spacing: -0.725px;
      color: #3d5170;
    }
  }
}
