.header {
  height: 55px;
  display: flex;
  align-items: center;
  background: #ffffff;
  box-shadow: 0px 2.5px 9.5px rgba(90, 97, 105, 0.12);
  padding: 0 8px 0 8px;

  .burgerMenuButtonWrapper {
    height: 32px;
    flex-grow: 1;

    .burgerButton {
      height: 100%;
      background: transparent;
      position: relative;
      border: none;

      .line {
        stroke-width: 2.5;
        stroke-linecap: round;
      }

      .burger {
        transition: all 0.2s;
      }

      &.active {
        .burger {
          transform: scale(0);
        }
        .x {
          transform: scale(1);
        }
      }

      .x {
        transform: scale(0);
        transition: all 0.2s;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }

  .notifications {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0.5px 0px 0px #cacedb, -0.5px 0px 0px #cacedb;
    text-align: center;
    flex-shrink: 0;
    min-width: 68px;

    .notificationsWrapper {
      height: 30px;
      width: 30px;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;

      .count {
        position: absolute;
        font-size: 9px;
        width: 12px;
        height: 12px;
        background: #c4183c;
        color: #ffffff;
        bottom: 0;
        right: 0;
        border-radius: 10px;
      }
    }
  }

  .userDetails {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 8px;
    min-width: 100px;
    padding-left: 16px;

    .dropdownIconWrapper {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      flex-grow: 1;
    }
  }
}
