.footer {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #ffffff;
  box-shadow: 0px -0.5px 0px #d7dae3;
  padding: 0 8px 0 8px;
  font-size: 12px;

  .links {
    display: flex;
    gap: 8px;

    .link {
      text-decoration: none;
      color: #007BFF;
    }
  }

  .license {
    font-weight: medium;
    color: #818EA3;
  }
}
